* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

/* NAVIGATION */

.nav-list{
	height: 10vh;
    display: flex;
    width: 50%;
	justify-content: space-around;
	align-items: center;
	margin-left: auto;
	list-style: none;
} 

.nav-links{
	text-decoration: none;
	color: #000000;
	font-size: 20px;
}

/* BACKGROUNDS */

.home,
.about{
	height: 90vh;
    position: absolute;
    width: 100%;
}

.home {
	background-image: linear-gradient(120deg, #a6c0fe 0%, #f68084 100%);
}

.about{
	background-image: radial-gradient(circle 248px at center, #16d9e3 0%, #30c7ec 47%, #46aef7 100%);
}

/* DIV CONTENT */

.home-content,
.about-content{
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}